@import url(https://fonts.googleapis.com/css?family=Lato);
* {
  margin: 0;
  padding: 0;
}

body {
    background-color: rgb(245, 245, 247);
    color: rgb(22, 22, 22);
    font-family: 'Lato', sans-serif;
}

.container {
    display: flex;
    flex-direction: row;
}
.lightsContainer {
    display: flex;
    flex-direction: column;
    max-width: 300px;
    width: 30%;
    height: calc(100vh - 100px);
    padding: 50px;
    overflow-y: scroll;
}

.lightsContainer input {
    padding: 20px;
    outline: none;
    border: none;
    border-radius: 5px;
    font-size: 15px;
    background-color: rgb(239, 239, 243);
}

.light {
    margin: 20px 0;
    background-color: white;
    border: 1px solid rgb(239, 239, 243);
    border-radius: 15px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    padding: 20px;
    cursor: pointer;
}

.light h3 {
    font-weight: bold;
    font-size: 22px;
}

.description {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    margin-top: 50px;
}

.description p {
    margin-bottom: 15px;
    color: rgb(136, 136, 136);
}

.color {
    width: 40px;
    height: 40px;
    border-radius: 5px;
}


.infoContainer {
    padding: 50px 0;
    background-color: white;
    width: 100%;
}

.grey {
    color: rgb(172, 172, 172);
}

.infoContainer p {
    font-size: bold;
}

.infoContainer h1 {
    margin-top: 20px;
}

.header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 50px;
}

.header img {
    float: right;
    margin-top: 10px;
    cursor: pointer;
}

.date {
    font-size: 20px;
}

.infoCardsContainer {
    display: grid;
    grid-auto-flow: column;
    padding: 50px;
    grid-column-gap: 50px;
    grid-auto-columns: 1fr;
}

.brightnessCard {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.brightnessCardHeader {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    padding: 20px;
}

.brightnessCard p {
    font-size: 20px;
    font-weight: bold;
}

.brightnessFooter {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

.brightnessFooter h1 {
    margin-bottom: 10px;
}

.rangeContainer {
    width: 80%;
    height: 50px;
    display: flex;
    align-items: center;
}

.input-range__label-container {
    display: none;
}

.effectCard {
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-direction: column;
}

.effectCard p {
    margin-top: 20px;
    font-size: 20px;
    font-weight: bold;
}

.buttonContainer {
    margin-bottom: 20px;
}

.buttonContainer > div {
    padding: 10px 20px;
    outline: none;
    border: none;
    border-radius: 5px;
    color: white;
    text-align: center;
    font-size: 15px;
    cursor: pointer;
    margin: 10px 0;
}

.switchContainer {
    width: 45px;
    height: 26px;
    border-radius: 14px;
    display: flex;
    align-items: center;
    padding: 0 3px;
}

.switchContainer div {
    width: 22px;
    height: 22px;
    background-color: white;
    border-radius: 22px;
}


